.noteContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  width: 500px;
}

.noteTitle {
  font-size: 2rem;
}

.searchBox {
  display: flex;
  width: 100%;
}

.searchBox div:nth-child(2) {
  flex-grow: 1;
}

.page_navi {
}

.page_navi li {
  display: inline-block;
  margin: 0 5px;
}
.page_navi li:hover {
  cursor: pointer;
}
/* .page li.active {
  font-weight: bold;
} */
.switch {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 17px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: '';
  height: 13px;
  width: 13px;
  left: 2px;
  bottom: 2px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(13px);
  -ms-transform: translateX(13px);
  transform: translateX(13px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 17px;
}

.slider.round:before {
  border-radius: 50%;
}
